import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';

import {
    Card,
    CardTitle,
    CardBody,
} from "reactstrap";

function AddLawyer() {
    const [validated, setValidated] = useState(false);
    const [states, setStates] = useState(["Maharashtra", "karanataka", "Goa"]);
    const [cities, setCities] = useState(["Nipani", "Pune", "Banglore"]);
    const [languages, setLanguage] = useState(["English", "Hindi", "Marathi"]);
    // const [speciality, setSpeciality] = useState(["Tax Law", "Corporate Law", "Criminal Law"]);


    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }

        setValidated(true);
    };

    return (
        <Row>
            <Col>
 
                <Card>
                    <CardBody>

                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>Full Name</Form.Label>
                                    <Form.Control
                                        required
                                        type="text"
                                        placeholder="Enter your full name"
                                    />
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 6}} controlId="validationCustom01">
                                    <Form.Label>Email ID</Form.Label>
                                    <Form.Control
                                        required
                                        type="email"
                                        placeholder="Enter your full email"
                                    />
                                </Form.Group>

                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label> Mobile Number</Form.Label>
                                    <Form.Control
                                        required
                                        type="number"
                                        placeholder="Enter your mobile number"
                                    />
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>State</Form.Label>
                                    <Form.Control required as="select" type="select" name="payment_method">
                                        <option value="">--- Select your State ---</option> 
                                        {
                                            states.map((state, index) => {
                                                return <option key={index} >{state}</option>
                                            })
                                        }
                                    </Form.Control>

                                </Form.Group>

                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>Firm Name</Form.Label>
                                    <Form.Control
                                        required
                                        type="number"
                                        placeholder="Enter your mobile number"
                                    />
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>Speciality</Form.Label>
                                    <Form.Control required as="select" type="select" name="payment_method">
                                        <option value="">--- Select your Speciality ---</option> 
                                        {
                                            states.map((state, index) => {
                                                return <option key={index} >{}</option>
                                            })
                                        }
                                    </Form.Control>

                                </Form.Group>

                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>City</Form.Label>

                                    <Form.Control required as="select" type="select" >
                                        <option value="">--- Select your City ---</option> 
                                        {
                                            cities.map((city, index) => {
                                                return <option key={index} required>{city}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 6 }} controlId="formFile" className="mb-3">
                                    <Form.Label>Resume/CV</Form.Label>
                                    <Form.Control required type="file" />
                                </Form.Group>
                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 6 }} controlId="validationCustom01">
                                    <Form.Label>Language</Form.Label>

                                    <Form.Control required as="select" type="select"   >
                                        <option value="">--- Select your language ---</option> 
                                        {
                                            languages.map((language, index) => {
                                                return <option key={index} >{language}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                            </Row>
                            <Row className="mb-2 mt-3">
                                <Form.Group as={Col} md={{ span: 6}} controlId="validationCustom01">
                                <Button type="submit">Submit</Button>
                                </Form.Group>
                            </Row>


                            
                        </Form>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
}
export default AddLawyer;

